<template>
  <div id="node-param">
    <el-table :data="nodeParams" height="290px">
      <el-table-column prop="name" label="参数名"> </el-table-column>
      <el-table-column prop="value" label="设定"> </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      nodeParamsList: {
        //项目参数
        default: [
          {
            name: "模型名称", //参数名称
            value: "vfsdfv", //输入框的值
            trigger: "noShow", //有这个属性则不展示下拉列表
            //提示列表
            tips: [
              { value: "100", otherData: {} },
              { value: "1000", otherData: {} },
              { value: "10000", otherData: {} },
            ],
          },
          {
            name: "迭代次数",
            value: "55",
            tips: [
              { value: "100", otherData: {} },
              { value: "1000", otherData: {} },
              { value: "10000", otherData: {} },
            ],
          },
          {
            name: "批次大小",
            value: "44",
            tips: [
              { value: "100", otherData: {} },
              { value: "1000", otherData: {} },
              { value: "10000", otherData: {} },
            ],
          },
          {
            name: "初始模型",
            value: "8898",
            tips: [
              { value: "模型1", otherData: {} },
              { value: "模型2", otherData: {} },
              { value: "模型3", otherData: {} },
            ],
          },
          {
            name: "入口函数",
            value: "是否SVSV",
            tips: [
              { value: "入口函数1", otherData: {} },
              { value: "入口函数2", otherData: {} },
              { value: "入口函数3", otherData: {} },
            ],
          },
        ],
      },
    };
  },
  computed: {
    // 可拖动组件节点的参数列表
    nodeParams() {
      let targetData = [];
      let sourceData = this.currentNodeDataVuex;
      targetData = Object.keys(sourceData).map((value) => {
        return { name: value, value: sourceData[value] };
      });
      return targetData;
    },
    nodeType() {
      return this.$store.state.projectManage.nodeParam.nodeType || "default";
    },
    // 当前被选中节点数据
    currentNodeDataVuex() {
      return this.$store.state.projectManage.itemShow.currentNodeData;
    },
  },
  methods: {
    handleSelect() {
      console.log("ss");
    },
  },
};
</script>

<style lang="scss" scoped>
.el-table {
  color: #ffff;
}
</style>

<style lang="scss">
#node-param {
  .el-table,
  .el-table__expanded-cell {
    background-color: transparent;
  }

  .el-table tr {
    background-color: transparent !important;
  }
  .el-table__body td,
  .el-table__header th,
  .el-table .cell {
    background-color: transparent;
  }
  .el-table::before {
    //去除底部白线
    left: 0;
    bottom: 0;
    width: 100%;
    height: 0px;
  }
  .el-table--scrollable-y .el-table__body-wrapper {
    width: calc(100% + 20px);
  }
}
</style>
