<template>
  <el-container id="ProjectManage">
    <!-- :visible.sync="dialogFormVisible" -->
    <el-Dialog
      title="创建新方案"
      :visible.sync="dialogFormVisible"
      :close-on-click-modal="false"
      :destroy-on-close="true"
    >
      <!-- <DialogFrom :dialogFormVisible.sync="dialogFormVisible"></DialogFrom> -->
      <DialogFrom v-if="dialogFormVisible"></DialogFrom>
    </el-Dialog>
    <el-header height="30px" class="project-manage-header">
      <!-- 功能按钮列表 与 项目名和状态 -->
      <el-row>
        <!-- 项目名和状态 -->
        <el-col :span="24"
          ><div class="item-status">
            <div class="item-status-title">
              {{ vuexResultListCurItem.name }}
            </div>
            <div class="btn-list">
              <el-button @click="createProject" type="success" size="mini"
                >新建</el-button
              >
              <el-button @click="editProject" type="success" size="mini"
                >编辑</el-button
              >
              <el-button
                :loading="loadingDelete"
                @click="onDeleteClick"
                type="success"
                size="mini"
                >删除</el-button
              >
              <el-button
                :loading="loadingDelete"
                @click="onLookClick"
                type="success"
                size="mini"
                >查看描述</el-button
              >
              <el-button
                :loading="loadingRun"
                @click="onRunClick"
                type="danger"
                size="mini"
                >启动</el-button
              >
            </div>
          </div>
        </el-col>
      </el-row></el-header
    >
    <el-container>
      <!-- 左侧边导航栏 -->
      <el-aside width="270px">
        <!-- <el-row class="aside-title">
          <el-col :span="24"><div class="aside-title-name"></div></el-col>
        </el-row> -->
        <el-row class="aside-list">
          <!-- <el-col :span="0"
            ><div class="aside-list-left">
              <AsideLeft></AsideLeft></div
          ></el-col> -->
          <el-col :span="24"
            ><div class="aside-list-right"><ResultList></ResultList></div
          ></el-col>
        </el-row>
      </el-aside>
      <!-- 右主体部分 -->
      <el-main>
        <!-- 项目展示区域 -->
        <el-row>
          <el-col
            ><div class="project-show" style="display: flex">
              <div class="item-show border-cricle">
                <ItemShow></ItemShow>
              </div>
              <div class="params-show">
                <div class="project-param border-cricle">
                  <div class="params-show-title">项目配置</div>
                  <div class="right-param-form">
                    <el-table :show-header="false" :data="runningConfig">
                      <el-table-column prop="name" label="参数名">
                      </el-table-column>
                      <el-table-column prop="value" label="设定">
                      </el-table-column>
                    </el-table>
                  </div>
                </div>
                <div class="item-param border-cricle">
                  <div class="params-show-title params-show-title-two">
                    参数配置
                  </div>
                  <div class="params-show-node"><NodeParam></NodeParam></div>
                </div>
              </div>
            </div>
          </el-col>
        </el-row>

        <!-- 运行状态图表 -->
        <el-row>
          <el-col :span="24">
            <div class="show-status">
              <div class="show-status-item show-status-table border-cricle">
                <StatusTable :tableData="currentStatusData"></StatusTable>
              </div>
              <div
                class="show-status-item show-status-charts border-cricle"
                v-for="(item, index) in statusChart"
                :key="index"
              >
                <StatusChart
                  :chartData="item"
                  :chartIndex="index"
                ></StatusChart>
              </div>
              <div class="show-status-item show-status-progress border-cricle">
                <StatusProgress :percentage="totalProcess"></StatusProgress>
              </div>
            </div>
          </el-col>
        </el-row>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
import AsideLeft from "./PageComponents/AsideLeft.vue";
import ResultList from "./PageComponents/ResultList.vue";
import StatusChart from "./PageComponents/StatusChart.vue";
import StatusProgress from "./PageComponents/StatusProgress.vue";
import StatusTable from "./PageComponents/StatusTable.vue";
import ItemShow from "./PageComponents/ItemShow.vue";
import NodeParam from "./PageComponents/NodeParam.vue";
import DialogFrom from "./PageComponents/DialogFrom.vue";

export default {
  components: {
    AsideLeft,
    ResultList,
    StatusChart,
    StatusProgress,
    StatusTable,
    ItemShow,
    NodeParam, //右侧节点参数
    DialogFrom, //新建项目弹窗
  },
  data() {
    return {
      StatusChart: {
        zero: [
          { tag: "模型评分", value: "81" },
          { tag: "损失值", value: "31" },
          { tag: "准确率", value: "12" },
          { tag: "F1", value: "21" },
        ],
        one: {
          title: "缺失值",
          xData: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
          series: [
            {
              data: [150, 230, 224, 218, 135, 147, 260],
              type: "line",
              areaStyle: {}, //显示阴影
            },
          ],
        },
        two: {
          title: "准确率",
          xData: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
          series: [
            {
              data: [15, 23, 24, 28, 35, 47, 60],
              type: "line",
              areaStyle: {}, //显示阴影
            },
          ],
        },
        three: {
          title: "损失率",
          xData: ["Mon", "Tue", "Wed", "Thu", "Fri", "Sat", "Sun"],
          series: [
            {
              data: [15, 23, 47, 60, 24, 28, 35],
              type: "line",
              areaStyle: {}, //显示阴影
            },
          ],
        },
      },
      //运行总进度
      totalProcess: 0,
      //实时运行状态
      realTimeData: {},
      //历史运行状态
      statusChart: [
        // {
        //   title: "缺失值",
        //   xData: [],
        //   yData: [],
        // },
      ],
      dialogFormVisible: false, // 新建项目的弹窗显影控制 false-隐藏
      // curProject: "", //当前选中的算法方案
      loadingRelease: false,
      loadingRun: false,
      loadingDelete: false,
      project_structure: {
        running_config: {
          initial_function: "入口函数1",
          initial_module: "初始模块1",
        },
      },
      pollingTimer: null, // 项目轮询计时器
    };
  },
  computed: {
    // 新建弹窗显隐控制
    vuexDialogFormVisible() {
      return this.$store.state.projectManage.dialogFrom.dialogFormVisible;
    },
    // 创建方案时填的数据
    // vuexRuleForm() {
    //   return this.$store.state.projectManage.dialogFrom.ruleForm;
    // },
    //当前选中的算法方案
    vuexResultListCurItem() {
      return this.$store.state.projectManage.resultList.curItem;
    },
    //运行配置
    runningConfig() {
      const ZW = { initial_function: "入口函数", initial_module: "初始模块" };
      let sourceData = this.$store.state.projectManage.itemShow.runningConfig;
      let targetData = Object.keys(sourceData).map((key) => {
        return { name: ZW[key], value: sourceData[key] };
      });
      return targetData;
    },
    //当前选中方案
    curItemVuex() {
      return this.$store.state.projectManage.resultList.curItem;
    },
    //
    initial_module() {
      return [1, 2];
    },
    //初始模块
    graphNodes() {
      return [];
    },
    //当前状态
    //实时运行状态
    currentStatusData() {
      return Object.keys(this.realTimeData).map((value) => {
        return { tag: value, value: this.realTimeData[value] };
      });
    },
  },
  watch: {
    vuexDialogFormVisible: {
      handler(newVal) {
        this.dialogFormVisible = newVal;
        // console.log(newVal, oldValue);
      },
    },
    // 当前项目选中状态
    curItemVuex: {
      handler(newVal) {
        if (this.pollingTimer) {
          clearTimeout(this.pollingTimer);
        }
        this.totalProcess = 0;

        this.statusChart = [
          {
            title: "暂无数据",
            xData: [],
            yData: [],
          },
          {
            title: "暂无数据",
            xData: [],
            yData: [],
          },
          {
            title: "暂无数据",
            xData: [],
            yData: [],
          },
        ];
        this.realTimeData = {};
        this.getAllProjectStatus();
        console.log(newVal);
      },
      deep: true,
    },
  },
  methods: {
    //   新建
    createProject() {
      this.dialogFormVisible = true;
      this.$store.commit(
        "projectManage/set_dialogFrom_dialogFormVisible",
        true
      );
    },
    //   编辑
    editProject() {
      this.$router.push({ name: "FlowChart" });
      console.log("d");
    },
    //  发布
    onReleaseClick() {
      this.loadingRelease = true;
      setTimeout(() => {
        this.$message({
          type: "success",
          message: "发布成功",
        });
        this.loadingRelease = false;
      }, 1000);
    },
    // 运行
    onRunClick() {
      this.loadingRun = true;
      this.reset();
      //请求参数
      // let params = { project: this.curItemVuex.name };
      let params = { id: this.curItemVuex.id };
      this.$api
        .runProject(params)
        .then((res) => {
          if (res.code === 200) {
            // this.getAllProjectStatus();

            this.getRealtimeProjectStatus();
            console.log(res);
            this.loadingRun = false;
          }
        })
        .catch((err) => {
          console.log(err);
          this.loadingRun = false;
        });
      // setTimeout(() => {
      //   this.$message({
      //     type: "success",
      //     message: "启动成功",
      //   });
      //   this.loadingRun = false;
      // }, 1000);
    },
    //删除
    onDeleteClick() {
      this.$confirm("此操作将永久删除该方案, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.loadingDelete = true;
          let itemName = this.curItemVuex.name;
          let routeName = this.$route.name;
          this.$api
            .deleteProject({ projectName: this.curItemVuex.name })
            .then((res) => {
              console.log(res);
              this.loadingDelete = false;
              this.$message({
                type: "success",
                message: "删除成功",
              });
              this.$store.commit(
                "projectManage/delect_resultList_isDelect",
                true
              );
              this.$store.commit("NavigationPage/delCompTag", {
                itemName: itemName,
                routeName: routeName,
              });
            })
            .catch((err) => {
              console.log(err);
              this.$message({
                type: "error",
                message: "删除失败",
              });
              this.loadingDelete = false;
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 查看
    onLookClick() {
      const that = this;
      this.$alert(
        `${that.vuexResultListCurItem.desc}`,
        that.vuexResultListCurItem.name,
        {}
      );
    },
    // 轮询获取项目实时状态数据
    getRealtimeProjectStatus() {
      let params = { id: this.curItemVuex.id };
      if (this.totalProcess >= 100) {
        return;
      }
      console.log("******获取项目实时数据*******");
      this.$api
        .getProjectStatusRealtime(params)
        .then((res) => {
          let statusChart = this.statusChart; //图表数据
          this.pollingTimer = setTimeout(() => {
            clearTimeout(this.pollingTimer);
            this.getRealtimeProjectStatus();
            // let historyData = res.result.run_real_time_msg;
            let realTime = res.result.run_real_time_msg;
            if (!realTime) {
              return;
            }
            // 当前状态数据为空
            if (
              statusChart.length === 0 ||
              !statusChart[0].title ||
              statusChart[0].title === "暂无数据"
            ) {
              this.statusChart = Object.keys(realTime)
                .filter((item) => item != "epoch")
                .map((value) => {
                  return {
                    title: value,
                    yData: [realTime[value]],
                    xData: [realTime["epoch"]],
                  };
                });
            }

            // 判断新获取的数据跟现有的数据是否相同
            if (
              !Object.keys(realTime).some((key) => {
                return "" + realTime[key] !== this.realTimeData[key] + "";
              })
            ) {
              return;
            }

            this.statusChart = this.statusChart.map((item) => {
              item.yData.push(realTime[item.title]);
              let len = item.xData.length;
              item.xData.push(len);
              return item;
            });

            this.realTimeData = realTime;
            // console.log(res.result);
            this.totalProcess = parseFloat(res.result.total_progress);
            // console.log(res);
          }, 500);
        })
        .catch((err) => {
          console.log(err);
        });
    },

    // 获取项目历史数据
    getAllProjectStatus() {
      console.log("******获取项目历史数据*******");
      let params = { id: this.curItemVuex.id }; //请求参数
      // let statusChart = this.statusChart; //图表数据
      // this.reset();

      this.$api
        .getProjectStatusAll(params)
        .then((res) => {
          console.log(res);
          // console.log(Object.keys(res.result).length > 0);
          if (Object.keys(res.result).length > 0) {
            let historyData = res.result.run_history_path;
            this.statusChart = Object.keys(historyData)
              .filter((item) => item != "epoch")
              .map((value) => {
                return {
                  title: value,
                  yData: historyData[value],
                  xData: [...historyData["epoch"]], //一定要是深拷贝
                };
              });
            let realTime = res.result.run_real_time_msg;
            this.realTimeData = realTime;
            this.totalProcess = parseFloat(res.result.total_progress);
            setTimeout(() => {
              this.getRealtimeProjectStatus();
            }, 0);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    // 切换项目时,清空项目配置信息和运行状态
    reset() {
      // 清空进度条
      this.totalProcess = 0;
      this.statusChart = [
        {
          title: "暂无数据",
          xData: [],
          yData: [],
        },
        {
          title: "暂无数据",
          xData: [],
          yData: [],
        },
        {
          title: "暂无数据",
          xData: [],
          yData: [],
        },
      ];
      this.realTimeData = {};
    },
  },
  created() {},
  mounted() {
    // this.getProjectStatus()
  },
  //使用keep-alive,进入页面时调用
  activated() {
    this.getAllProjectStatus();
  },
  //使用keep-alive,离开页面页面时调用
  deactivated() {
    clearTimeout(this.pollingTimer);
  },
  beforeDestroy() {
    clearTimeout(this.pollingTimer);
  },
};
</script>

<style lang="scss" scoped>
@import "~assets/scss/Navigation/ProjectManage/ProjectManage.scss";
</style>

<style lang="scss">
.right-param-form {
  .el-table {
    color: aliceblue;
  }
  .el-table,
  .el-table__expanded-cell {
    background-color: transparent;
  }

  .el-table tr {
    background-color: transparent !important;
  }
  .el-table__body td,
  .el-table__header th,
  .el-table .cell {
    background-color: transparent;
  }
  .el-table::before {
    //去除底部白线
    left: 0;
    bottom: 0;
    width: 100%;
    height: 0px;
  }
  .el-table tbody tr:hover > td {
    // background-color: transparent;
    background-color: #3c4a88;
  }
}
</style>
