<template>
  <div class="status-echart" ref="echart">echart</div>
</template>

<script>
import echarts from "echarts";
export default {
  //props接受父组件传过来的参数
  props: {
    chartData: {
      type: Object,
      default() {
        return {
          title: "",
          xData: [],
          series: [],
        };
      },
    },
    //判断图表是否有坐标轴
    //true 是有
    chartIndex: {
      type: Number,
      default: 0,
    },
  },
  computed: {
    options() {
      return this.axisOption;
    },
    dataChart() {
      return this.chartData;
    },
  },
  watch: {
    chartData: {
      handler() {
        this.initChart();
      },
      deep: true,
      //   immediate: true,
    },
  },
  data() {
    return {
      //图表容器
      echart: null,
      //   有坐标轴
      axisOption: {
        title: {
          text: "无数据",
          textStyle: {
            color: "#ffff",
          },
          left: 20,
        },
        // 控制图表位置
        grid: {
          left: "40",
          top: "40",
          bottom: "20",
          right: 20,
        },
        //鼠标放在图表上提示
        tooltip: {
          // 触发类型 ：坐标轴触发
          trigger: "axis",
        },
        // // 图例
        // legend: {
        //   textStyle: {
        //     color: '#333',
        //   },
        // },
        //x轴
        xAxis: {
          type: "category",
          boundaryGap: false,
          data: [],
          //轴线颜色
          axisLine: {
            lineStyle: {
              color: "#17b3a3",
            },
          },

          axisLabel: {
            color: "#ffff",
          },
        },
        //y轴
        yAxis: {
          type: "value",
          axisLine: {
            lineStyle: {
              color: "#17b3a3",
            },
          },
          axisLabel: {
            color: "#ffff",
          },
        },
        // 折线颜色
        color: "#222222",
        //图表数据
        series: [
          {
            data: [],
            type: "line",
            areaStyle: {}, //显示阴影
          },
        ],
      },
      colorList: [
        "#2ec7c9",
        "#b6a2de",
        "#5ab1ef",
        "#ffb980",
        "#d87a80",
        "#8d98b3",
        "#e5cf0d",
        "#97b552",
        "#95706d",
        "#dc69aa",
        "#07a2a4",
        "#9a7fd1",
        "#588dd5",
        "#f5994e",
        "#c05050",
        "#59678c",
        "#c9ab00",
        "#7eb00a",
        "#6f5553",
        "#c14089",
      ],
    };
  },
  methods: {
    //初始化图表
    initChart() {
      console.log("initChart");
      //初始化数据
      this.initChartData();
      if (this.echart) {
        this.echart.setOption(this.options);
      } else {
        this.echart = echarts.init(this.$refs.echart);
        this.echart.setOption(this.options);
      }
    },
    // 判断是否为有坐标轴的图表
    initChartData() {
      this.axisOption.title.text = this.chartData.title;
      this.axisOption.xAxis.data = this.chartData.xData;
      let colorListLength = this.colorList.length;
      this.axisOption.color = this.colorList[this.chartIndex % colorListLength];
      this.axisOption.series[0].data = this.chartData.yData;
    },
    // 当浏览器窗口大小发生改变时，修改图表大小，达到自适应的效果
    resizeChart() {
      this.echart ? this.echart.resize() : "";
    },
  },
  mounted() {
    this.initChart();
    // 事件监听
    // 当'resize' 事件发生时，调用resizeChart方法
    window.addEventListener("resize", this.resizeChart);
  },
  destroyed() {
    window.removeEventListener("resize", this.resizeChart);
  },
};
</script>

<style lang="scss" scoped>
.status-echart {
  overflow: hidden;
  height: 100%;
  width: 100%;
}
</style>
